@import url('https://fonts.googleapis.com/css?family=Amatic%20SC');
.plant-details {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Amatic SC';
  }
  
  .plant-details-header {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    font-family: 'Amatic SC';
    width: 100%;
  }
  h1 {
    margin: 0;
  }
  .back-button {
    padding: 1rem;
    position: absolute;
    left: 0;
    cursor: pointer;
  }
  
  .plant-details-info {
    display: flex;
    justify-content: space-between;
    font-family: 'Amatic SC';
    width: 100%;
    max-width: 350px;
  }
  .plant-info {
    background-color: #f0f0f0;
    padding: 1rem;
  }
  
  .watering-log ul {
    font-family: 'Amatic SC';
    list-style-type: none;
    background-color: #f0f0f0;
    padding: 1rem;
  }
  .watering-log li {
    margin: 1rem 0rem;
  }
  
  .watering-reminder-button,
  .remove-plant-button,
  .water-plant-now-button,
  .update-plant-details-button {
    cursor: pointer;
    margin-top: 10px;
    background-color: f0f0f0;
    border-radius: 10px;
    border: none; /* remove default button border */
    padding: 10px 20px; /* add some padding for better button appearance */
  }
Switch {
    margin-left: 1rem;
    margin-right: 1rem;
}
.left-switch,.right-switch {
    width: 100px;
}
.right-switch {
    text-align: right;
}


