.App {
  font-family: 'Amatic SC';
  text-align: center;
}

.plant-shelf {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 0rem;
}

.plant {
  background-color: #f0f0f0;
  padding: 1rem;
}

h1 {
  margin-bottom: 2rem;
}
.bottom-buttons {
  padding: 2rem;
}
button {
  cursor: pointer;
  margin-top: 10px;
  background-color: f0f0f0;
  border-radius: 10px;
  border: none; /* remove default button border */
  padding: 10px 20px; /* add some padding for better button appearance */
}