.add-plant {
    font-family: 'Amatic SC';
    text-align: center;
  }
.add-to-shelf {
    margin-top: 10px;
    background-color: f0f0f0;
    border-radius: 10px;
    border: none; /* remove default button border */
    padding: 10px 20px; /* add some padding for better button appearance */
}

.carousel .slide img {
    max-height: 300px; 
    max-width: 300px;
}
.plant-details-header {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    font-family: 'Amatic SC';
    width: 100%;
  }
h1 {
    margin: 0;
}
.back-button {
    padding: 1rem;
    position: absolute;
    left: 0;
    cursor: pointer;
}
  